<template>
  <el-form
    :rules="rules"
    class="login-container"
    label-position="left"
    label-width="0px"
    v-loading="loading"
  >
    <h3 class="login_title"></h3>

    <el-form-item prop="empId">
      <el-input type="text" v-model="loginForm.empId" auto-complete="off" placeholder="ID"></el-input>
    </el-form-item>

    <el-form-item prop="username">
      <el-input type="text" v-model="loginForm.username" auto-complete="off" placeholder=""></el-input>
    </el-form-item>

    <el-form-item prop="checkPass">
      <el-input type="password" v-model="loginForm.password" auto-complete="off" placeholder=""></el-input>
    </el-form-item>

    <el-form-item prop="checkPass">
      <el-input
        type="password"
        v-model="loginForm.rePassword"
        auto-complete="off"
        placeholder=""
      ></el-input>
    </el-form-item>

    <el-form-item prop="checkPass">
      <el-input type="text" v-model="loginForm.veriCode" auto-complete="off" placeholder=""></el-input>
    </el-form-item>

    <br />
    <el-form-item style="width: 100%">
      <el-button
        type="success"
        size="mini"
        style="width: 100%"
        :disabled="checked"
        @click="sendCode()"
      >{{content}}</el-button>
      <br />
      <el-button type="primary" size="mini" style="width: 100%" @click="submitClick"></el-button>

      <el-button type="primary" size="mini" style="width: 30%;margin: auto;background-color:pink; border: white" @click="gorefresh"></el-button>
      <br />
      <!--<a href="/"></a>-->
    </el-form-item>
  </el-form>
</template>



<script>
  /*
      author: HASI
      time: 2019/10/15 0015 10:35
      function:.postRequest("/register/sendVerfiCode"
  */
export default {
  name: "register",
  data() {
    return {
      rules: {
        //account: [{required: true, message: '', trigger: 'blur'}],
        //   checkPass: [{required: true, message: '', trigger: 'blur'}]
      },
      content: "", // 
      totalTime: 60, //
      checked: false,
      loginForm: {
        empId: "",
        username: "",
        password: "",
        rePassword: "",
        veriCode: "",
        codeId: ""
      },
      loading: false
    };
  },
  methods: {
    gorefresh(){
      this.$router.push({path: '/'});
    },
    sendCode: function() {
      this.postRequest("/register/sendVerfiCode", this.loginForm).then(
        msg => {
          this.loading = false;
          if (msg && msg.status == 200) {
            console.log(msg);
            this.loginForm.codeId = msg.data.obj.veriId;
            this.countDown();
          }
        }
      );
    },

    submitClick: function() {
      this.postRequest("/register/register", this.loginForm).then(
        msg => {
          this.loading = false;
          if (msg && msg.status == 200) {
            console.log(msg);
            alert("");
          }
        }
      );
    },
    countDown() {
      let clock = window.setInterval(() => {
        this.totalTime--;
        this.content = this.totalTime + "s";
        if (this.totalTime > 0) {
          this.checked = true;
        }
        if (this.totalTime < 0) {
          window.clearInterval(clock);
          this.checked = false;
          this.totalTime = 60;
          this.content = "";
        }
      }, 1000);
    }
  }
};
</script>

<style scoped>
.login-container {
  border-radius: 15px;
  background-clip: padding-box;
  margin: 50px auto;
  width: 350px;
  height: 500px;
  padding: 35px 35px 15px 35px;
  background: #fff;
  border: 1px solid #eaeaea;
  box-shadow: 0 0 25px #cac6c6;
}
.login_title {
  margin: 0px auto 40px auto;
  text-align: center;
  color: #505458;
}
.login_remember {
  margin: 0px 0px 35px 0px;
  text-align: left;
}
</style>



